.toggle-btn{
    height: 25px;
    display: flex;
    /* margin-right: 20px; */
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0 1rem;
    box-sizing: border-box;
    justify-content: space-around;
    flex-direction: column;
    
}
.toolbar__navigation{
    display: flex;
    align-items: center;
}

.toggle-btn.focus{
    outline: none;
}
.toggle-button__line{
    width: 30px;
    height: 2px;
    background: white;
}
.toggle-btn{
    margin-left: 10px;
    align-items: center;
    
    
    
}


@media (max-width: 600px){
    .toolbar_navigation-items{
        display: none;
    }
}

@media (min-width: 600px){
    .toggle-button{
        display: none;
    }
}