.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    width: 60%;
    max-width: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);
}
.NavLinks{
    display: flex;
    flex-direction: column;
    text-decoration: none;
    justify-content: center;
    padding: 20px;
}

